body {
    margin: 0;
    font: 16px/1.6, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    color: #fff;
    background: #f9fbfc;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

a{
    color: #111;
    text-decoration: none;
}

#root{
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

main{
    flex:1;
    padding:10px 100px;
    color:#000;
}